<template>
  <q-page class="page-with-background">
    <div class="page-content">
    <q-card flat class="q-mt-lg">
      <q-card-section>
        <div class="text-h5">Proben ohne Krach - Herzlich Willkommen und viel Spaß!</div>
      </q-card-section>

      <q-card-section v-if="!isBackendAvailable">
        <backend-status></backend-status>
      </q-card-section>

      <q-card-section horizontal class="q-ml-xl">
          <div>
            <div v-if="wifi_qr_image_url">
              <img :src="wifi_qr_image_url" alt="Wifi QR code" class="q-mt-md" />
            </div>
            <div class="text-weight-bold q-mt-md">Wifi-Zugang</div>
            <div>WLAN: {{  wifi_sid  }} <br>Passwort: {{ wifi_password }}</div>
          </div>
          <div style="width: 200px;"></div>
          <div>
            <div v-if="control_qr_image_url">
              <img :src="control_qr_image_url" alt="App QR code" class="q-mt-md" />
            </div>
            <div class="text-weight-bold q-mt-md">Raumsteuerung</div>
          </div>

          <div>
            <q-btn v-if='!show_backup_qr' class='q-ml-xl' no-caps label="Backup QR" @click="show_backup_qr = true" />
            <div v-if="control_qr_image_url2 && show_backup_qr" class="q-ml-xl">
            <img :src="control_qr_image_url2" alt="App QR code" class="q-mt-md" />
          </div>
          </div>
      </q-card-section>

      <q-card-section>
        <ol>
          <li>Verbinde dich zuerst über den linken QR-Code mit unserem Proberaum-Wifi.</li>
          <li>Anschließend kannst du den rechten QR-Code nutzen, um die Instrumente über dein Telefon/Tablet vom Platz aus zu steuern.</li>
        </ol>
       </q-card-section>

      <q-card-section>
        <div class="text-h6">Du brauchst Hilfe?</div>
        Bei Problemen bitte einfach unter <span class='text-weight-bold'>{{ contact_info }}</span> melden. Wir helfen dann so schnell wie möglich weiter.
      </q-card-section>

    </q-card>
  </div>
  </q-page>

</template>

<script>
import BackendStatus from '@/components/BackendStatus.vue';
import { useInstrumentStore } from '@/stores/instruments-store';
import { getWifiQrImage, getControlQrImage, getControlQrImage2, getRoomStatus } from '@/services/apiServices';

export default {
  setup () {
  },
  data() {
    return {
      show_backup_qr: false,
      wifi_qr_image_url: null,
      control_qr_image_url: null,
      control_qr_image_url2: null,
      wifi_sid: 'ProbenOhneKrach',
      wifi_password: '12345678',
      contact_info: '0151 12345678',
    }
  },
  components: {
    BackendStatus,
  },
  created() {
    this.fetchImages();
    this.fetchRoomStatus();
  },
  methods: {
    fetchRoomStatus()
    {
      getRoomStatus()
        .then(response => {
          this.wifi_sid = response.data.wifi_sid;
          this.wifi_password = response.data.wifi_password;
          this.contact_info = response.data.contact_info;
        }).catch((error) => {
          console.error('Error fetching room status:', error);
        });
    },
    fetchImages() {
      getWifiQrImage()
        .then(response => {
          this.wifi_qr_image_url = URL.createObjectURL(response.data);
        }).catch((error) => {
          console.error('Error fetching wifi QR code:', error);
        });

      getControlQrImage()
        .then(response => {
          this.control_qr_image_url = URL.createObjectURL(response.data);
        }).catch((error) => {
          console.error('Error fetching control QR code:', error);
        });

      getControlQrImage2()
        .then(response => {
          this.control_qr_image_url2 = URL.createObjectURL(response.data);
        }).catch((error) => {
          console.error('Error fetching control QR code:', error);
        });
    },
  },
  computed: {
    isBackendAvailable() {
      const store = useInstrumentStore();
      return store.isBackendAvailable;
    }
  }
}
</script>

